      }
    },
    setCallBack () {
      this.showAction = false
    },
    appShareFn (index) {
      // index 1 微信好友 2 微信朋友圈 3 为qq
      let msg = {
        title: this.fatherParams.title,
        content: this.fatherParams.desc,
        thumbs: [this.fatherParams.img_url],
        href: this.fatherParams.shareLink
      }
      let id = 'weixin'
      if (index === 1) {
        id = 'weixin'
        msg.type = 'web'
        msg.extra = {
          scene: 'WXSceneSession'
        }
      } else if (index === 2) {
        id = 'weixin'
        msg.type = 'web'
        msg.extra = {
          scene: 'WXSceneTimeline'
        }
      } else if (index === 3) {
        id = 'qq'
        // msg.type = 'text'
      } else if (index === 4) {
        id = 'sinaweibo'
      }
      setTimeout(() => {
        // w.close()
      }, 5000)
      if (index === 4 && !this.commonState.shares['sinaweibo'].nativeClient) {
        this.$toast({
          message: '请先安装新浪微博',
          duration: 1500
        })
        return
      } else if (index === 4 && this.commonState.shares['sinaweibo'].nativeClient && !this.commonState.shares['sinaweibo'].authenticated) {
        this.commonState.shares['sinaweibo'].authorize(function () {
          this.commonState.shares['sinaweibo'].send(msg, function (res) {
            this.$toast({
              message: '分享成功',
              duration: 1500
            })
          }, err => {
            // w.close()
            this.showShare = false
            if (err.code === -2) {
              this.$toast({
                message: '已取消分享',
                duration: 1500
              })
              this.shareSuccess({
                type: 'app',
                way: '3',
                success: 2
              })
            } else {
              this.$toast({
                message: '分享失败',
                duration: 1500
              })
            }
          })
        })
      }
      this.commonState.shares[id].send(msg, res => {
        if (index === 3) {
          // w.close()
          this.$toast({
            message: '分享成功',
            duration: 1500
          })
        }
        this.setCallBack({type: 'app', way: index})
        this.showShare = false
      }, err => {
        // w.close()
        this.showShare = false
        if (err.code === -2) {
          this.$toast({
            message: '已取消分享',
            duration: 1500
          })
          this.setCallBack({
            type: 'app',
            way: '3',
            success: 2
          })
        } else {
          this.$toast({
            message: '分享失败',
            duration: 1500
          })
        }
      })
    },
    toPage () {
      this.$router.push('/AwardFeedback')
    },
    setIndex (tabIndex, index) {
      this.$router.push({
        name: 'QuestionDetail',
        params: {
          tab: tabIndex,
          index
        }
      })
      // for (let i of this.questionList.keys()) {
      //   if (tabIndex === this.activeIndex) {
      //     for (let j of this.questionList[i].list.keys()) {
      //       if (index === j) {
      //         this.$set(this.questionList[i].list[j], 'active', !this.questionList[i].list[j].active)
      //       } else {
      //         this.$set(this.questionList[i].list[j], 'active', false)
      //       }
      //     }
      //   } else {
      //     continue
      //   }
      // }
    },
    setShow (index) {
      this.activeIndex = index
    },
    closeShare () {
      this.showConfirm = false
    },
    shareSuccess () {

    }
  },
  created () {
    if (this.$config.source_style >= 6) {
      this.download = false
    } else if (this.$utils.isWx()) {
      if (this.$route.query.inviteCode) {
        this.download = true
      } else {
        this.download = false
      }
    } else {
      this.download = true
    }
  },
  beforeRouteLeave (to, from, next) {
    this.$utils.cache('questionDetail', this.$refs.richQuestion.scrollTop)
    next()
  },
  activated () {
    if (this.$utils.cache('questionDetail')) {
      this.$nextTick(() => {
        this.$refs.richQuestion.scrollTop = this.$utils.cache('questionDetail')
      })
    }
  },
  mounted () {
    this.$utils.cache('questionDetail', null)
    this.fatherParams = {
      title: '掘金宝资讯-赚车赚房计划介绍',
      desc: '掘金宝（原始股）提现、升值、锁粉、玩法1分钟全面介绍，让你轻松上手玩转掘金宝',
      img_url: 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/make_money.png'
    }
    this.appShare.path = this.$route.path
    let linkParams = {
      path: this.appShare.path ? this.appShare.path : '/' // 传递地址进来
    }
    if (this.commonState.login_bol) {
      linkParams.inviteCode = this.commonState.userInfo.invitation
    }
    if (this.$config.source_style >= 6) {
      this.fatherParams.shareLink = `${this.$config.currentWebUrl}/?${this.$qs.stringify(linkParams)}`
    } else {
      this.fatherParams.shareLink = `${location.origin}/?${this.$qs.stringify(linkParams)}`
    }
    this.showShare = true
    this.$nextTick(() => {
      for (let i of this.questionList.keys()) {
        for (let j of this.questionList[i].list.keys()) {
          let height = this.$refs[`contentEL${i}${j}`][0].clientHeight
          this.$set(this.questionList[i].list[j], 'height', height + 'px')
        }
      }
    })
  }
}
</script>

<style lang="scss" scoped>
.scrollBox {
   // 分享
  .share-group {
    position: fixed;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 30px;
    box-sizing: border-box;
    align-items: center;
    height: 100px;
    border-top: 1px solid #eee;
    background: #fff;
    font-size: 28px;
    &.isIos {
      .sharebutton {
        padding-top: 20px;
      }
    }
    .sharebutton {
      text-align: center;
      padding: 22px 0 14px;
      line-height: 34px;
      color: #FF7F18;
      border: 1px solid #FF7F18;
      width: 240px;
      line-height: 40px;
      height: 80px;
      box-sizing: border-box;
      border-radius: 70px;
      &.active {
        background: #F38D32;
        color: #fff;
        width: 360px;
      }
    }
  }
}
#rich-question {
  overflow: auto;
  height: 100vh;
  box-sizing: border-box;
  padding-bottom: 100px;
  &.Router {
    transition: all 0s ease;
  }
  .banner {
    height: 240px;
    background: #f5f5f5;
  }
  .rect {
    height: 20px;
    background: #f5f5f5;
  }
  // app
  .intro {
    padding: 30px 0;
    .intro-content {
      background: #FFFFFF;
      box-shadow: -2px -2px 4px 0 #DADADA, 2px 2px 4px 0 #DADADA;
      border-radius: 15px;
      margin: 0 auto;
      width: 710px;
      margin: 0 auto;
      padding: 10px 30px 20px;
      box-sizing: border-box;
      .intro-item {
        border-bottom: 1px solid #E5E5E5;
        padding: 20px 0;
        &:last-child{
          border-width: 0;
        }
        .intro-title {
          padding-bottom: 20px;
          font-size: 32px;
          color: #333333;
          font-weight: 900;
          line-height: 45px;
        }
        .intro-text {
          .tag {
            color: #BE9251;
          }
          font-size: 28px;
          color: #333333;
          line-height: 50px;
          margin-bottom: 15px;
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }
  }
  // 折叠栏
  .question {
    padding-bottom: 200px;
    .tabList {
      display: flex;
      padding: 0 70px;
      justify-content: space-between;
      border-bottom: 1px solid #E5E5E5;
      .tab-item {
        padding: 20px 0 22px;
        position: relative;
        font-size: 36px;
        line-height: 50px;
        &.active{
          color: #F90000;
          &::after{
            content: '';
            display: block;
            position: absolute;
            left: 50%;
            width: 94px;
            background: #F90000;
            margin-left: -47px;
            bottom: 0;
            height: 4px;
          }
        }
      }
    }
    .tab-content-list {
      overflow: hidden;
      height: 0;
      &.hidden {
        height: auto;
      }
      .question-title {
        padding: 30px 0;
        font-weight: 900;
        font-size: 36px;
        color: #FF6D02;
        text-align: center;
      }
      .title {
        margin: 0 30px;
        padding: 28px 0;
        border-bottom: 1px solid #f2f2f2;
        line-height: 42px;
        font-size: 32px;
        justify-content: space-between;
        .text {
          text-align: justify;
          flex: 1;
          font-size: 30px;
          color: #333;
          padding-right: 50px;
        }
        .arrowEl {
          border-color: #B4B4B4;
          width: 16px;
          height: 16px;
          transition: transform 400ms;
          &.active {
            transform: rotateZ(225deg);
          }
        }
      }
      .content {
        height: 0;
        overflow: hidden;
        transition: height 400ms;
        background: #F4F5F7;
        .contentInner {
          padding: 22px 30px 25px;
          font-size: 30px;
          color: #666666;
          line-height: 50px;
          > div {
            margin-bottom: 20px;
            &:last-child {
              margin-bottom: 0;
            }
          }
        }
      }
    }
  }
 
}
</style>



