<template>
  <div id="friend">
    <!-- <headerNav v-if="$utils.showNavBar() && !userInfo && commonState.userInfo.is_new === 0"></headerNav> -->
    <div class="invite_pre">
      <div class="headerNav">
        <div class="leftDiv" @click="back"></div>
        <div class="title">{{$route.meta.title}}</div>
      </div>
      <div class="inviteInfo">
        <div class="title">我是由Ta邀请的</div>
        <div class="userInfo flex-row jusB" v-if="userInfo">
          <div class="userInfo-left flex-row">
            <div class="avatar iconSize">
              <img :src="userInfo.avatar" alt="">
            </div>
            <div class="nickname">{{userInfo.nickname}}</div>
          </div>
          <div class="userInfo-right f24 color999">{{$utils.timestampToStr(userInfo.invitee_time * 1000, 'Y-M-D h:m')}}</div>
        </div>
        <div class="noInviteInfo" v-else>
          <div class="leftTitle">您还没有上级邀请人</div>
          <div class="inviteButton" v-if="!userInfo && commonState.is_new === 0" @click="toUrl('/setInviteCode')">输入邀请码</div>
        </div>
      </div>
    </div>
    <div class="invite_next" :class="{hasNavBar: $utils.showNavBar() && !userInfo && commonState.userInfo.is_new === 0, hasInvite: commonState.userInfo.is_new === 1 || userInfo}">
      <div class="record flex-row jusB">
        <div class="r-title">我的邀请记录</div>
        <div class="right flex-row" @click="showTip = true">
          <div class="r-text">邀请好友未实时到账</div>
          <div class="arrowEl"></div>
        </div>
      </div>
      <div class="inviteList" ref="inviteList" v-if='inviteList.length > 0 || !complete'>
        <div class="scrollEl">
          <list-view v-if="inviteList.length > 0" :list='inviteList'></list-view>
          <div class="list-no-data f26" v-if="isLoading">加载中...</div>
          <div class="list-no-data f26" v-if="inviteList.length > 0 && complete">没有更多邀请数据了</div>
        </div>
      </div>
      <div class="inviteList flex-row" v-if="inviteList.length === 0 && complete">
        <div class="noMessage">
          <div class="messageIcon iconSize">
            <img src="../../../assets/img/details/invite_message.png" alt="消息">
          </div>
          <div class="tip color999">你还没有邀请过好友</div>
        </div>
      </div>
      <div class="inviteBottom flex-row colorFFF bold f30" @click="toUrl('/invite_friend')">继续赚掘金宝</div>
    </div>
    <tip-model v-if="showTip" @closeFn="showTip = false"></tip-model>
  </div>
</template>
<script>
import {mapState, mapMutations} from 'vuex'
import headerNav from '@/components/task/header-nav'
import ListView from './list'
import BScroll from 'better-scroll'
import TipModel from './model'
export default {
  components: {
    ListView,
    headerNav,
    TipModel
  },
  data () {
    return {
      showTip: false,
      userInfo: null,
      inviteList: [],
      loaded: true,
      page: 1,
      isLoading: false,
      complete: false
    }
  },
  computed: {
    ...mapState({
      commonState: state => state.common
    })
  },
  methods: {
    ...mapMutations([
      'common_login_box',
      'common_set_userInfo'
    ]),
    back () {
      this.$router.go(-1)
    },
    getList (cb) {
      let data = {
        page: this.page,
        status: 1
      }
      this.$api.get('INVITE_LIST', data, res => {
        this.loaded = true
        // res.data.data = [{
        //   nickname: '阿斯顿发斯蒂芬三分',
        //   create_time: Number(new Date().getTime() / 1000),
        //   vcoin: 1000
        // }]
        if (res.data.data && res.data.data.length > 0) {
          if (this.page === 1) {
            this.inviteList = res.data.data
          } else {
            this.inviteList = [...this.inviteList, ...res.data.data]
          }
          console.log(this.inviteList)
        }
        console.log(res.data.data)
        if (this.page >= res.data.last_page) {
          this.complete = true
        }
        this.page++
        this.loadData()
        typeof cb === 'function' && cb(res.data)
      })
    },
    getInvite () {
      this.$api.get('MYINVITEUSER', {}, res => {
        this.userInfo = res.data
        this.loadData()
      })
    },
    loadData () {
      this.$nextTick(() => {
        if (!this.scroll) {
          if (this.$refs.inviteList) {
            this.scroll = new BScroll(this.$refs.inviteList, {
              click: true,
              probeType: 3,
              pullUpLoad: {
                threshold: 300,
                stop: 0
              },
              mouseWheel: {
                speed: 10,
                invert: false,
                easeTime: 300
              },
              bounceTime: 400,
              scrollbar: {
                fade: true,
                interactive: false
              }
            })
            this.scroll.on('pullingUp', () => {
              this.upList()
            })
          }
        } else {
          this.scroll.refresh()
        }
      })
    },
    upList () {
      if (this.isLoading || this.complete) {
        return
      }
      this.isLoading = true
      this.getList(res => {
        this.scroll.finishPullUp()
        this.isLoading = false
      })
    },
    // 邀请人
    toUrl (url) {
      this.$router.push(url)
    },
    initData () {
      this.getList()
      this.getInvite()
    }
  },
  mounted () {
    this.initData()
    this.loadData()
  },
  watch: {
    'commonState.login_box' (flag) {
      if (!flag && this.commonState.login_bol) {
        this.inviteList = []
        this.userInfo = null
        this.isLoading = false
        this.complete = false
        this.initData()
      }
    }
  }
}
</script>
<style lang="scss" scoped>
#friend {
  height: 100vh;
  background: #f5f5f5;
  .invite_pre {
    background: #FFAA00;
    height: 200px;
    .headerNav {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 88px;
      color: #fff;
      font-size: 34px;
      line-height: 1.5;
      position: relative;
      font-weight: 600;
      .leftDiv {
        padding-left: 30px;
        position: absolute;
        left: 0;
        height: 100%;
        width: 20px;
        padding: 0 30px;
        background: url('../../../assets/img/white_back.png') no-repeat center;
        background-size: 20px 35px;
      }
    }
    .inviteInfo {
      background: #FFFFFF;
      box-shadow: 0 2px 10px 0 #E0E0E0;
      border-radius: 10px;
      margin: 15px 30px 0;
      padding: 30px 30px 40px;
      box-sizing: border-box;
      background: #fff;
      height: 216px;
      .title {
        font-weight: 900;
        line-height: 45px;
      }
      .userInfo {
        margin-top: 33px;
        .avatar {
          width: 76px;
          height: 76px;
          margin-right: 20px;
          border-radius: 50%;
          overflow: hidden;
        }
        .nickname {
          font-size: 30px;
        }
      }
      .noInviteInfo {
        display: flex;
        margin-top: 44px;
        height: 50px;
        align-items: center;
        justify-content: space-between;
        .leftTitle {
          font-size: 28px;
          color: #666666;
        }
        .inviteButton {
          width: 194px;
          height: 50px;
          box-sizing: border-box;
          padding-top: 10px;
          background: #F4A82B;
          border-radius: 25px;
          text-align: center;
          color: #fff;
          line-height: 38px;
          font-size: 26px;
        }
      }
    }
  }
  .invite_next {
    background: #fff;
    background: #fff;
    min-height: 500px;
    background: #fff;
    height: calc(100% - 350px);
    position: relative;
    margin-top: 150px;
    &.hasHeader {
      height: calc(100% - 88px);
    }
    .record {
      padding-top: 30px;
      background: #fff;
      height: 96px;
      line-height: 36x;
      box-sizing: border-box;
      padding: 31px 30px 20px;
      .right {
        font-size: 26px;
        line-height: 38px;
        color: #4A90E2;
        .arrowEl {
          width: 14px;
          height: 14px;
          border-color: #4A90E2;
        }
      }
    }
    .inviteList {
      height: calc(100% - 240px);
      overflow: hidden;
      position: relative;
      box-sizing: border-box;
      .list-no-data {
        text-align: center;
        color: #999;
        line-height: 80px;
      }
      .noMessage {
        display: flex;
        justify-content: center;
        flex-flow: column wrap;
        .messageIcon {
          width: 169px;
          height: 107px;
          margin: 0 auto;
        }
        .tip {
          margin-top: 45px;
          font-size: 30px;
          line-height: 1.2
        }
      }
      
    }
  }
  .scrollEl {
    padding-bottom: 60px;
  }
  .inviteBottom {
    position: absolute;
    left: 30px;
    width: 690px;
    background: #F5A623;
    border-radius: 37.5px;
    height: 75px;
    bottom: 50px;
    // margin: 30px;
  }
}
</style>



