<template>
  <div class="list">
    <div class="listItem" v-for="(item, index) in list" :key="index">
       <!-- 正常状态 -->
      <div class="inviteItem flex-row jusB">
        <div class="leftContent flex-row">
          <div class="leftIcon iconSize">
            <img :src="item.invitee_avatar" alt="头像">
          </div>
          <div class="nickname">{{$utils.splitStr(item.invitee_nickname, 4)}}</div>
        </div>
        <div class="rightContent">
          <div class="">{{item.create_time ? $utils.timestampToStr(item.create_time * 1000, 'Y-M-D h:m:s') :'无'}}</div>
          <div class="vcoin redColor">{{item.vcoin ? (item.vcoin * 1).toFixed(2) : 0}}掘金宝</div>
          <!-- <div class="iconSize loss" v-else>
            <img src="../../../assets/img/details/loss.jpg" alt="已失效">
          </div> -->
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    list: {
      type: Array
    }
  }
}
</script>
<style lang="scss" scoped>
.noList{
    .flex-row{
      &:nth-child(1) {
        padding: 156px 0 30px;
      }
      .iconSize{
        width: 169px;
        height: 107px;
      }
    }
    
  }
  .inviteItem {
    padding: 20px 30px;
    font-size: 30px;
    color: #232323;
    border-bottom: 1px solid #d8d8d8;
    .leftIcon{
      width: 70px;
      height: 70px;
      margin-right: 20px;
      overflow: hidden;
      border-radius: 50%;
    }
    .nickname {
      width: 170px;
      min-width: 0;
      line-height: 2;
      margin-right: 20px;
      text-align: left;
    }
    .rightContent{
      font-size: 24px;
      flex: 1.3;
      white-space: nowrap;
      color: #999;
      display: flex;
      align-items: center;
      justify-content: space-between;
      line-height: 2;
      .loss {
        width: 114px;
        height: 45px;
      }
    }
    .statusButton {
      margin-left: 14px;
      background: #999;
      font-size: 26px;
      color: #fff;
      border-radius: 44px;
      box-sizing: border-box;
      width: 104px;
      height: 44px;
      span {
        padding-top: 4px;
      }
    }
    &.failure {
      background: #eee;
      .rightContent {
        justify-content: center;
      }
    }
  }
</style>



