<template>
  <div id="friendModel" @touchmove.prevent="">
    <div class="mask-content">
      <div class="title">邀请奖励未实时到账？</div>
      <div class="content">
        <div class="content-text">1、首先确认邀请人登录了掘金宝，在你的好友列表里有此人（需要上拉好友列表多翻几页查看，一般都是有这个遗漏）；</div>
        <div class="content-text">2、若看到他在你的好友列表里，就不用担心，掘金宝奖励已经发放，只是推送消息有延迟或丢失（邀请奖励功能已经过严格测试不会出错）</div>
        <div class="content-text">3、请以好友列表为准，不要以消息为准，消息推送偶尔有遗漏。</div>
      </div>
      <div class="close" @click="closeFn"></div>
    </div>
  </div>
</template>
<script>
export default {
  methods: {
    closeFn () {
      this.$emit('closeFn')
    }
  }
}
</script>

<style lang="scss" scoped>
#friendModel {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba($color: #000000, $alpha: .5);
  .mask-content {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 600px;
    margin-top: -60px;
    border-radius: 20px;
    background: #fff;
    .title {
      height: 120px;
      padding: 0 30px;
      overflow: hidden;
      border-radius: 20px 20px 0 0;
      text-align: center;
      color: #fff;
      line-height: 56px;
      padding: 42px 0 22px;
      line-height: 56px;
      font-size: 36px;
      font-weight: 900;
      box-sizing: border-box;
      background: linear-gradient(to right, #feb400 0%, #ffda00 100%);
    }
    .content {
      padding-bottom: 50px;
    }
    .content-text {
      margin: 30px;
      font-size: 32px;
      line-height: 45px;
    }
    .close {
      width: 60px;
      height: 60px;
      background: url('../../../assets/img/task/close.png') no-repeat center;
      background-size: 45px 45px;
      position: absolute;
      bottom: -100px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
</style>



